<template>
  <div class="agency-chain">
    <div @touchmove.prevent class="chain-tab">
      <div class="chain-tab-ul">
        <div class="chain-tab-li">
          <router-link :to="'agencySalesman'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">业务员</span>
            </div>
          </router-link>
        </div>
        <div class="chain-tab-li">
          <router-link :to="'agencyTJT'" replace>
            <div class="chain-tab-li-hot">
              <span class="chain-name">美导</span>
            </div>
          </router-link>
        </div>
      </div>
    </div>
    <keep-alive>
        <router-view @touchstart.native="scrollFixStart" @touchmove.native="scrollFixMove" class="chain-tab-view"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "agency-chain",
  data() {
    return {};
  },
  components: {},
  activated() {},
  created() {},
  computed: {
    ...mapState(["openid", "path"])
  },
  methods: {
    scrollFixStart(e) {
      var el = e.currentTarget;
      if (el.scrollTop === 0) {
        el.scrollTop = 1;
      } else if (el.scrollHeight == el.scrollTop + el.offsetHeight) {
        el.scrollTop = el.scrollTop - 1;
      }
    },
    scrollFixMove(e) {
      var el = e.currentTarget;
      if (el.scrollHeight > el.clientHeight) {
        e.stopPropagation();
      }
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.agency-chain {
  width: 100%;
  flex: 1;
  display: flex;
  flex-flow: column nowrap;

  .chain-tab-view {
    padding-top 8px;
    flex: 1;
    overflow: auto;
    // -webkit-overflow-scrolling: touch;
    overflow-x: hidden;
  }

  .chain-tab {
    height: 36px;
    background-color: #ffffff;
    position relative;
    z-index 1;
    box-shadow: 0 1px 1px rgba(80, 80, 80, 0.2);

    .chain-tab-ul {
      margin: 0px auto;
      height: 100%;
      width: 80%;
      display: flex;
      align-items: center;
      text-align: center;

      .chain-tab-li {
        overflow: hidden;
        height: 100%;
        flex: 1;

        .chain-tab-li-hot {
          width: 100%;
          height: 100%;
          position: relative;

          .chain-name {
            line-height: 36px;
            color: #8a8a8a;
            font-size: 14px;
            position: relative;
            top: 1px;
          }
        }

        .chain-tab-li-hot::after {
          position: absolute;
          bottom: -3px;
          left: 15%;
          width: 70%;
          height: 2px;
          background: #8B6969;
          border-radius: 1px;
          content: '';
          -webkit-transition: -webkit-transform 0.2s;
          transition: transform 0.2s;
          -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
        }

        .active {
          .chain-name {
            top: 0px;
            color: #8B6969;
            font-size: 15px;
          }

          .chain-tab-li-hot {
            // background-color: #ffffff;
          }

          .chain-tab-li-hot::after {
            -webkit-transform: translate3d(0, -150%, 0);
            transform: translate3d(0, -150%, 0);
          }
        }
      }
    }
  }
}
</style>



